import QSPopup from '@/components/QS-popup/QS-popup.vue';
export default{
	components:{QSPopup},
	data(){
		return{
			renew:false,  //检查更新状态
			version:'1.0.0'
		}
	},
	onLoad() {



	},
	methods:{
		show(ref) {
			this.$refs[ref].show();
		},
		hides(ref) {
			this.$refs[ref].hide();
		},
		clear(){
			uni.setStorageSync('selectArr',[]);
			uni.showToast({
				title:'清除缓存成功!',
				icon:'none',
				duration:2000
			})
		},
		getversion(){
			let version = plus.runtime.version
			this.version = version
		},
		loginOut(){
			uni.setStorageSync('token','');
			uni.setStorageSync('selectArr',[]);
			uni.navigateTo({
				url:'/pages/index/logon/logon'
			})
		},
	}
}
